@import url(https://fonts.googleapis.com/css?family=Lato:400,700);
body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  color:#fff;
  background-color: rgb(207, 31, 40);
}


button{
	cursor: pointer;
}
.bounding-box {
	position: absolute;
	box-shadow: 0 0 0 3px #149df2 inset;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	cursor: pointer;;

}
div{
	font-family: Lato;
}
.form{
