    border-width: 2px;
}

button{
	background-color:  #ADA79B;
}

.App {
  text-align: center;
}

.Rank {
	-webkit-transform: translate3d(25px,0px,0px);
	        transform: translate3d(25px,0px,0px);
	}

.center{
  display:flex;
